// close button
.off-canvas {
	.close-button {
		span {
		    display: block;
		    font-size: 35px;
		    color: #fff;
		    font-weight: 300;
		    margin: 14px 0 0 25px;
		    line-height: 1;
		    cursor: pointer;
		}
	}
}

// nav links
aside.right-off-canvas-menu {
	
	a {
		color: #96B5FF;
		display: block;
		padding: 10px 30px;
		
		&:hover {
			color: #fff;
			background-color: $secondary-color;
			text-decoration: none;
		}
	}
}